<template>
	<div>
    <div class="block">
      <div>
        <el-table
          ref="filterTable"
          :data="tableData"
          @filter-change="filterOrg"
          stripe
          style="width: 100%"
          height="750">
          <el-table-column type="expand">
            <template slot-scope="props">
              <el-form label-position="left" inline class="demo-table-expand">
                <el-form-item label=": ">
                  <span>{{ props.row.id }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.org }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.perName }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.perTel }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.perAddr }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.addr }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.code }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.contName }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.contTel }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.status }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.start }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.end }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.pick }}</span>
                </el-form-item>
              </el-form>
            </template>
          </el-table-column>
          <!--  -->
          <el-table-column
            type="index"
            :index="indexMethod"
            width="100">
          </el-table-column>
          <el-table-column
            type="selection"
            width="70">
          </el-table-column>
          <el-table-column
            label=""
            prop="id"
            width="170">
          </el-table-column>
          <el-table-column
            label=""
            prop="org"
            width="150"
            :filters="filters"
            column-key="org"
            filter-placement="bottom-end">
            <template slot-scope="scope">
              <el-tag
                :type="scope.row.org === '' ? 'primary' : 'success'"
                disable-transitions>{{scope.row.org}}</el-tag>
            </template>
          </el-table-column>
          <el-table-column
            label=""
            prop="perName"
            width="150">
          </el-table-column>
          <el-table-column
            label=""
            prop="code"
            width="150">
          </el-table-column>
          <el-table-column
            label=""
            prop="start"
            width="200">
          </el-table-column>
          <el-table-column
            label=""
            prop="end"
            width="200">
          </el-table-column>
          <el-table-column
            label=""
            prop="pick"
            width="100">
          </el-table-column>
          <el-table-column
            align="right"
            width="300">
            <template slot="header" slot-scope="scope">
              <el-input
                v-model="search"
                size="mini"
                placeholder=""
                @keyup.enter.native="searchHandler"/>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          background
          :hide-on-single-page="value"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="pageSize"
          layout="total, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
	import Constant from "../pub/Constant"
  export default {
		name: "AdminIsPack",
    data() {
      return {
        baseUrl: Constant.data.baseUrl,
        currentPage: 1,       // 
        total: 21,            // 
        pageSize: 10,         // 
        search: '',
        tableData: [{
          id: '12987122',
          org: '',
          perName: 'user1',
          perTel: '12345678900',
          perAddr: '',
          addr: '',
          code: '1-1-16',
          contName: '',
          contTel: '12345678910',
          status: '',
          start: '2020-12-28 10:24:00',
          end: '',
          pick: ''
        }],
        filters: [],
        orgFilter: ''
      }
    },
    methods: {
		  // 
      handleSizeChange(val) {
        console.log(` ${val} `)
      },
      // 
      handleCurrentChange(val) {
        console.log(`: ${val}`)
        this.getPacks(this.orgFilter)
      },
      // 
      searchHandler() {
        this.$message({
          showClose: true,
          type: 'info',
          message: ''
        })
      },
      // 
      filterOrg(filters) {
        let org = filters.org
        console.log(org)   // 
        this.orgFilter = org
        if (org === '' || org === null) {
          this.getPacks("")
        } else {
          this.getPacks(org)
        }
      },
      getPacks(org) {
        let param = new URLSearchParams()
        let token = localStorage.getItem("token")
        let jsonParam = {
          "currentPage" : this.currentPage,
          "pageSize" : this.pageSize,
          "token" : token,
          "org" : org
        }
        param.append('json', JSON.stringify(jsonParam))
        const _this = this
        console.log("")
        this.$axios({
          method: 'post',
          url: _this.baseUrl + '/pack/getAdminIsPick',
          data: param
        })
          .then(function (response) {
            console.log("")
            console.log(response.data)
            if (response.data.fail === 'get info fail') {
              _this.$notify({
                showClose: true,
                title: '',
                message: '',
                type: 'warning'
              })
              _this.$router.push('/loginAndRegister')
            } else {
              _this.total = response.data.pack_result.total
              _this.tableData = response.data.pack_result.list
            }
          })
          .catch(function (error) {
            console.log(error)
            _this.$notify.error({
              showClose: true,
              title: '',
              message: ''
            })
          })
      },
      indexMethod(index) {
        return (this.currentPage - 1) * this.pageSize + index + 1
      },
      setFilters() {
        const _this = this
        let card = localStorage.getItem("card")
        if (card === '2101') {
          _this.filters = [{ text: '', value: '' }
            , { text: '', value: '' }
            , { text: '', value: '' }]
        } else if (card === '2102') {
          _this.filters = [{ text: '', value: '' }
            , { text: '', value: '' }
            , { text: '', value: '' }]
        } else {
          _this.filters = [{ text: '', value: '' }
            , { text: 'EMS', value: 'EMS' }]
        }
      }
    },
    created() {
		  this.setFilters()
      this.getPacks("")
    },
    mounted() {
      this.setFilters()
		  // this.getPacks()
    }
  }
</script>

<style scoped>
  .demo-table-expand {
    font-size: 0;
    font-weight: bold;
  }

  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }

  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>



